@import url(https://fonts.googleapis.com/css2?family=Karla:wght@500&display=swap);
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Karla', sans-serif;
}

.App-link {
  color: #61dafb;
}

.App-header {
  background-color: #fff2e0;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.name {
  display: inline;
  font-size: 130px;
}

.school {
  font-size: 28px;
}

.links {
  font-size: 1.2rem;
  padding: 10px;
  display: flex;
  justify-content: space-between;
}

.scroll {
  margin: 20px;
}
.my-navbar {
  background-color: #fff2e0;
}

.navbar-header {
  background-color: white;
}

.navbar-title {
  font-size: 1.5rem;
  text-decoration: none !important;
}

.navbar-option,
.navbar-title {
  color: black !important;
}

.navbar-option:hover,
.navbar-title:hover {
  color: red !important;
}

.my-active .navbar-title {
  color: red !important;
}

.my-active .lead navbar-option {
  color: red !important;
}

.my-active .nav-link {
  color: red !important;
}

.navbar-toggle {
  color: black !important;
}

.nav-link {
  color: black !important;
}
.title {
  font-size: 3rem;
}

.description {
  text-align: left;
  font-size: 1.2rem;
}

.image {
  max-width: 60%;
  position: relative;
  z-index: 1;
}

#experience {
  background-color: #ffe4e0;
}

.experience-title {
  font-size: 3.3rem;
}

.job-heading {
  font-size: 2.3rem;
}

@media (max-width: 767px) {
  .job-heading {
    text-align: center;
  }
}

.job-subheading {
  font-size: 1.3rem;
}

.job-description {
  text-align: left;
}

.blueprint-logo {
  width: 110%;
}
.carleton-logo {
  max-width: 60%;
}
#projects {
  background-color: white;
}

.experience-title {
  font-size: 3.3rem;
}

.job-heading {
  font-size: 2.3rem;
}

@media (max-width: 767px) {
  .job-heading {
    text-align: center;
  }
}

.job-subheading {
  font-size: 1.3rem;
}

.job-description {
  text-align: left;
}

.blueprint-logo {
  width: 110%;
}
.stocks-img {
  max-width: 150%;
}

.logos * {
  margin: 10px;
}

.logos img {
  width: 50px;
  length: 50px;
}

#projects a {
  color: black;
  text-decoration: none;
}
#contact {
  background-color: #fff2e0;
}

.contact-title {
  color: black;
  font-size: 2.5rem;
}

.copyright {
  color: black;
  font-size: 1rem;
}
